/* eslint-disable max-len */
import Divider from '@buzzguru/ui/Divider';
import Card from '@buzzguru/ui/kit/BlurCard';
import DoubleColumnChart from '@buzzguru/ui/kit/DoubleColumnChart';
import LineChart from '@buzzguru/ui/kit/LineChart';
import MobileTabs from '@buzzguru/ui/Tabs';
import Typography from '@buzzguru/ui/Typography2';
// import { isClient, isServer } from '@lskjs/env';
import Select from '@lskjs/form-control-select';
import { Row } from '@lskjs/grid';
import T from '@lskjs/t';
import { toShort } from '@lskjs/utils/formatter';
import get from 'lodash/get';
import { inject, observer } from 'mobx-react';
import moment from 'moment';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import beauty from '../../utils/beauty';
import Wrapper, { ChartDates, Col, Header, StyledStatistic } from './Audience.styles';
import AudiencePlaceholder from './AudiencePlaceholder';
import DropdownIndicator from './DropdownIndicator';

const Audience = ({ entity, i18, blur, blurProps, ...props }) => {
  const { item, loading = true } = entity || {};
  const [activeTab, setActiveTab] = useState(2);
  const [showDateBy, setShowDateBy] = useState(30);
  const [chartType, setChartType] = useState('default');
  const [dynamics, setDynamics] = useState([]);
  const [initialized, setInitialized] = useState(false);
  const getChartDate = (order = 'first') => {
    const data =
      activeTab === 3
        ? (dynamics || []).slice(0, showDateBy).map(([date]) => date)
        : (dynamics || []).map(([date]) => moment(date).format('DD MMMM'));
    if (order === 'last') return data[data.length - 1];
    return data[0];
  };
  const mobileAudienceChart = (
    <Col sm={12}>
      {chartType === 'default' ? (
        <>
          <LineChart
            format="DD MMMM"
            data={activeTab === 3 ? (dynamics || []).slice(0, showDateBy) : dynamics.map((d) => d.slice(0, 2))}
            dash={activeTab === 3}
            disableFormatX={activeTab === 3}
            offsetYmax={10}
            disableXLabels
            tooltipRenderer={({ points, dates, chart }) => {
              const date = dates[points[0].x].locale(i18.t('locale')).format('DD MMMM');
              let daysLocale = '';
              if (activeTab === 3) {
                daysLocale = i18.t('audienceChart.daysLocale_3');
                if (activeTab === 3 && (chart.x + 1) % 10 === 1 && chart.x + 1 !== 11) {
                  daysLocale = i18.t('audienceChart.daysLocale_1');
                }
                if (activeTab === 3 && [2, 3, 4].includes((chart.x + 1) % 10) && ![12, 13, 14].includes(chart.x + 1)) {
                  daysLocale = i18.t('audienceChart.daysLocale_2');
                }
              }
              return `
              <div style="display: flex; flex-direction: column; align-items: flex-start;">
                <div style="font-size: 9px">
                  ${activeTab === 3 ? chart.x + 1 : date} ${daysLocale}
                </div>
                <div style="display: flex; align-items: center; font-size: 11px; font-weight: 500">
                <div style="background-color:#7070ff;width:8px;height:8px;border-radius:50%;margin-right:5px; margin-bottom: 2px;"></div>
                ${i18.t(`channel.audience.button.default_${activeTab}`)}: ${toShort(chart.y)}</div>
                </div>
              `;
            }}
          />
          <ChartDates>
            <div>{getChartDate('first')}</div>
            <div>{getChartDate('last')}</div>
          </ChartDates>
        </>
      ) : (
        <>
          <DoubleColumnChart
            format="DD MMMM"
            data={(dynamics || []).map((d) => d.filter((_, i) => i !== 1))}
            disableXLabels
            offsetY={10}
            tooltipRenderer={({ date, chart }) => {
              const count = chart.y < 0 ? `-${beauty(Math.abs(chart.y))}` : beauty(chart.y);
              return `
              <div style="display: flex; flex-direction: column; align-items: flex-start;">
                <div>${date}</div>
                <b style="display: flex; align-items: center;">
                <div style="background-color:#7070ff;width:8px;height:8px;border-radius:50%;margin-right:5px; margin-bottom: 2px;"></div>
                ${i18.t(`channel.audience.button.growth_${activeTab}`)}: ${count}</b>
                </div>
              `;
            }}
          />
          <ChartDates>
            <div>{getChartDate('first')}</div>
            <div>{getChartDate('last')}</div>
          </ChartDates>
        </>
      )}
    </Col>
  );
  const handleChangeShowDateBy = async ({ tab, value }) => {
    if (!item) return;
    setDynamics([]);
    setShowDateBy(value);
    switch (tab) {
      case 1: {
        if (value) {
          item.subscribersDynamics2.getFindParams = () => ({ _id: get(item, '_id'), type: 'day', value, delts: true });
          await item.subscribersDynamics2.fetch();
          setDynamics(
            (get(item, 'subscribersDynamics2.item', []) || []).map((fItem) => [
              get(fItem, '0'),
              get(fItem, '1'),
              get(fItem, '2'),
            ]),
          );
        } else {
          item.subscribersDynamics2.getFindParams = () => ({
            _id: get(item, '_id'),
            type: 'month',
            value: 12,
            delts: true,
          });
          await item.subscribersDynamics2.fetch();
          setDynamics(
            (get(item, 'subscribersDynamics2.item', []) || []).map((fItem) => [
              get(fItem, '0'),
              get(fItem, '1'),
              get(fItem, '2'),
            ]),
          );
        }
        break;
      }
      case 2: {
        if (value) {
          item.viewsDynamics2.getFindParams = () => ({ _id: get(item, '_id'), type: 'day', value, delts: true });
          await item.viewsDynamics2.fetch();
          setDynamics(
            (get(item, 'viewsDynamics2.item', []) || []).map((fItem) => [
              get(fItem, '0'),
              get(fItem, '1'),
              get(fItem, '2'),
            ]),
          );
        } else {
          item.viewsDynamics2.getFindParams = () => ({ _id: get(item, '_id'), type: 'month', value: 12, delts: true });
          await item.viewsDynamics2.fetch();
          setDynamics(
            (get(item, 'viewsDynamics2.item', []) || []).map((fItem) => [
              get(fItem, '0'),
              get(fItem, '1'),
              get(fItem, '2'),
            ]),
          );
        }
        break;
      }
      case 3: {
        if (value) {
          await item.forecastViews.fetch();
          setDynamics((get(item, 'forecastViews.item', []) || []).map((fItem) => [get(fItem, '0'), get(fItem, '1')]));
        }
        break;
      }
      default:
        break;
    }
  };
  useEffect(() => {
    if (get(item, 'viewsDynamics2.item') && !initialized) {
      setInitialized(true);
      setDynamics(
        (get(item, 'viewsDynamics2.item', []) || []).map((fItem) => [
          get(fItem, '0'),
          get(fItem, '1'),
          get(fItem, '2'),
        ]),
      );
    }
  }, [get(item, 'viewsDynamics2.item')]);
  useEffect(() => {
    if (activeTab === 3) {
      setChartType('default');
      setShowDateBy(30);
    }
  }, [activeTab]);

  const handleChangeChartType = (val) => {
    setChartType(val);
  };
  const handleChangeActiveTab = (val) => {
    setDynamics([]);
    setActiveTab(val);
    handleChangeShowDateBy({ tab: val, value: showDateBy });
  };
  let totalViews = 0;
  if (Array.isArray(get(item, 'viewsDynamics2.item'))) {
    totalViews = (get(item, 'viewsDynamics2.item') || [])
      .filter((_, index) => index === get(item, 'viewsDynamics2.item.length', 0) - 1)
      .map((fItem) => get(fItem, '1', 0));
  }
  let totalForecasts = 0;
  if (Array.isArray(get(item, 'forecastViews.item')) && get(item, 'forecastViews.item').length) {
    totalForecasts = (get(item, 'forecastViews.item') || [])
      .filter((_, index) => index === 29)
      .map((fItem) => get(fItem, '1', 0));
  }
  const totalSubscribers = get(item, 'channelInfo.subscribers', 0);
  const mobileStatistic = () => {
    switch (activeTab) {
      case 1:
        return (
          <StyledStatistic
            title={totalSubscribers.toLocaleString('ru-RU')}
            subtitle={<T name="channel.audience.tab.subscribers" />}
          />
        );
      case 2:
        return (
          <StyledStatistic
            title={totalViews.toLocaleString('ru-RU')}
            subtitle={<T name="channel.audience.tab.views" />}
          />
        );
      case 3:
        return (
          <StyledStatistic
            title={totalForecasts.toLocaleString('ru-RU')}
            subtitle={<T name="channel.audience.tab.forecast" />}
          />
        );
      default:
        return (
          <StyledStatistic
            title={totalSubscribers.toLocaleString('ru-RU')}
            subtitle={<T name="channel.audience.tab.subscribers" />}
          />
        );
    }
  };
  const getOptions = (optionType, postfix = '') => {
    if (optionType === 'chartType') {
      const chartTypeOptions = [
        {
          value: 'default',
          title: <T name={`channel.audience.button.default_${activeTab}${postfix}`} />,
        },
        {
          value: 'growth',
          title: <T name={`channel.audience.button.growth_${activeTab}${postfix}`} />,
        },
      ];
      if (activeTab === 3) chartTypeOptions.pop();
      return chartTypeOptions;
    }
    let showDateByOptions = [
      {
        value: 7,
        title: <T name="channel.audience.button.7day" />,
      },
      {
        value: 14,
        title: <T name="channel.audience.button.14day" />,
      },
      {
        value: 30,
        title: <T name="channel.audience.button.30day" />,
      },
      {
        value: 90,
        title: <T name="channel.audience.button.90day" />,
      },
      {
        value: null,
        title: <T name="channel.audience.button.all" />,
      },
    ];
    if (activeTab === 3) showDateByOptions = showDateByOptions.slice(0, 3);
    return showDateByOptions;
  };

  if (loading || (!item && loading)) {
    return <AudiencePlaceholder />;
  }

  const mobileChartTypeOptions = getOptions('chartType', 'Mobile');

  return (
    <div name="audience">
      <Wrapper name="audience">
        <Divider margin="20 0 8" />
        <Header view="heading-1" lineHeight={3.73}>
          <T name="channel.audience.title" />
        </Header>
        <Row vertical>
          <Col sm={12}>
            <Card insetRight={0} insetLeft={0} insetTop={0} blur={blur} blurProps={blurProps} {...props}>
              <Card.Content>
                <Row vertical>
                  <Col sm={12} style={{ padding: '15px 25px' }}>
                    <MobileTabs
                      active={activeTab}
                      items={[
                        {
                          id: 2,
                          title: <T name="channel.audience.tab.viewsMobile" />,
                          onClick: () => handleChangeActiveTab(2),
                        },
                        {
                          id: 1,
                          title: <T name="channel.audience.tab.subscribersMobile" />,
                          onClick: () => handleChangeActiveTab(1),
                        },
                        {
                          id: 3,
                          title: <T name="channel.audience.tab.forecastMobile" />,
                          onClick: () => handleChangeActiveTab(3),
                        },
                      ]}
                    />
                    {mobileStatistic()}
                    <Row>
                      <Col xs={6}>
                        {activeTab === 3 ? (
                          <Typography style={{ padding: '16px 14px 14px' }} view="title-2">
                            {mobileChartTypeOptions[0].title}
                          </Typography>
                        ) : (
                          <Select
                            key={`react-select-${chartType}-${activeTab}`}
                            components={{ DropdownIndicator }}
                            onChange={handleChangeChartType}
                            value={chartType}
                            isSearchable={false}
                            isClearable={false}
                            options={mobileChartTypeOptions}
                          />
                        )}
                      </Col>
                      <Col xs={6}>
                        <Select
                          key={`react-select-${showDateBy}-${activeTab}`}
                          components={{ DropdownIndicator }}
                          onChange={(val) => handleChangeShowDateBy({ tab: activeTab, value: val })}
                          value={showDateBy}
                          isSearchable={false}
                          isClearable={false}
                          options={getOptions('showDateBy', 'Mobile')}
                        />
                      </Col>
                    </Row>
                  </Col>
                  {mobileAudienceChart}
                </Row>
              </Card.Content>
            </Card>
          </Col>
        </Row>
      </Wrapper>
    </div>
  );
};

Audience.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  uapp: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object),
  blur: PropTypes.bool,
  blurProps: PropTypes.objectOf(Object),
};

Audience.defaultProps = {
  i18: { t: () => {} },
  blur: false,
  blurProps: {},
};

export default inject('i18')(observer(Audience));
