/* eslint-disable global-require */
import EntityStore from '@lskjs/mobx/mobxStores/EntityStore';
import Err from '@lskjs/utils/Err';
import { createElement } from 'react';

import MainLayout from '../layouts/MainLayout';
import ErrorPage from '../pages/ErrorPage';
import HomePage from '../pages/HomePage';

export default {
  path: '',
  async action({ next, page }) {
    return page.next(next).catch((err) => {
      if (err && !['page.cancel'].includes(err.code)) {
        const pageParams = {
          err,
        };

        pageParams.children = createElement(ErrorPage, pageParams);

        return page.component(MainLayout, pageParams);
      }
      throw new Err('E_404', err);
    });
  },
  children: [
    {
      path: '',
      children: [
        {
          path: '',
          async action({ page, app }) {
            const ChannelStore = await app.module('stores.ChannelStore');

            const entity = new EntityStore({
              api: ChannelStore,
              getFindParams: () => ({
                _id: '5bb03391e8b4730017891656',
                populate: [
                  {
                    path: 'relations.channels',
                    select: [
                      'channelInfo.avatar',
                      'channelInfo.title',
                      'channelInfo.subscribers',
                      'type',
                      'scores',
                      'medians',
                      'ratings',
                    ],
                  },
                ],
              }),
            });

            entity.fetch().then(() => {
              // try {
              //   entity.item.fetchViewsDynamics2({ type: 'day', value: 30, delts: true });
              //   entity.item.fetchSubscribersDynamics2({ type: 'day', value: 30, delts: true });
              //   entity.item.fetchForecastViews();
              // } catch (error) {
              //   console.log('entity.fetch', error);
              // }
            });

            const pageParams = {
              entity,
            };

            pageParams.children = createElement(HomePage, pageParams);

            return page.component(MainLayout, pageParams);
          },
        },
      ],
    },
    {
      path: '(.*)',
      action({ path, page }) {
        const err = {
          status: 404,
          message: `Not found path ${path}`,
          path,
        };
        const pageParams = {
          err,
        };

        pageParams.children = createElement(ErrorPage, pageParams);

        return page.component(MainLayout, pageParams);
      },
    },
  ],
};
