/* eslint-disable no-console */
import asyncMapValues from '@lskjs/utils/asyncMapValues';
import ready from '@lskjs/utils/polyfill';
import Bluebird from 'bluebird';

import * as plugins from './plugins';
import { injectCss, injectJs } from './utils';
import { includes } from './utils/includes';

/* global window */
const { log } = window.__lskjs;

ready();

const jses = ['https://cdn.jsdelivr.net/npm/siiimple-toast/dist/siiimple-toast.min.js'];
const csses = [
  // 'https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css',
  'https://cdn.jsdelivr.net/npm/siiimple-toast/dist/style.css',
];
const ignoreDomains = ['://drive.google.com', '://docs.google.com', '://rabbitmq.buzz.guru'];

const main = async () => {
  const location = document?.location?.href || '';
  if (includes(location, ignoreDomains)) {
    log.trace('GLOBAL IGNORE', location);
    return;
  }
  try {
    await Bluebird.map(jses, (item) => injectJs(item));
    await Bluebird.map(csses, (item) => injectCss(item));
  } catch (err) {
    log.error('[loading static]', err);
  }
  await asyncMapValues(plugins, async (plugin, name) => {
    try {
      log.trace(`[${name}] starting`);
      const res = await plugin();
      log.trace(`[${name}] ok`, res);
    } catch (err) {
      log.error(`[${name}] err`, err);
    }
  }).catch((err) => {
    log.error('[loading plugins]', err);
  });
};

main();
