import React, { useState } from 'react';

import Trigger from './components/Trigger';
import { Wrapper } from './Toolbar.styles';

export const ToolbarContext = React.createContext({ isOpen: false });

const Toolbar = ({ children }) => {
  const [isOpen, setOpen] = useState();

  const triggerOpen = () => {
    setOpen(!isOpen);
  };

  return (
    <Wrapper>
      <ToolbarContext.Provider value={{ isOpen }}>{children}</ToolbarContext.Provider>
      <Trigger onClick={triggerOpen} />
    </Wrapper>
  );
};

export default Toolbar;
