import VerifiedIcon from '@buzzguru/ui/assets/icons/verified';
import Avatar from '@buzzguru/ui/Avatar';
import CardSimple from '@buzzguru/ui/CardSimple';
import Label from '@buzzguru/ui/Label';
import PropTypes from 'prop-types';
import React from 'react';

import * as Styles from './InfluencerHeaderCardMobile.styles';

const InfluencerHeaderCardMobile = ({ avatar, image, title, isVerified, tags, actions, children, ...props }) => (
  <CardSimple insetTop={8} insetBottom={24} insetLeft={8} insetRight={8} radius={12} {...props}>
    <Styles.ImageWrapper>
      <Avatar square="square" src={image} title={title} width="100%" height="100%" />
    </Styles.ImageWrapper>
    <Styles.ContentWrapper>
      <Styles.AvatarContainer>
        <Styles.AvatarWrapper>
          <Avatar src={avatar} title={title} size={116} />
        </Styles.AvatarWrapper>
      </Styles.AvatarContainer>
      <Styles.Content>
        <Styles.InfoWrapper>
          <Styles.Info>
            <Styles.TitleWrapper>
              {title && (
                <Styles.Title as="h1" view="huge-11">
                  {title}
                </Styles.Title>
              )}
              {isVerified && (
                <Styles.VerifiedWrapper>
                  <VerifiedIcon />
                </Styles.VerifiedWrapper>
              )}
            </Styles.TitleWrapper>
            <Styles.TagsWrapper>
              {tags &&
                tags.slice(0, 2).map((tag, index) => {
                  let commonProps = {};
                  if (tag.href) {
                    commonProps = {
                      componentClass: 'a',
                      href: tag.href,
                    };
                  }
                  if (tag.onClick && !tag.href) {
                    commonProps = {
                      componentClass: 'button',
                      onClick: tag.onClick,
                    };
                  }
                  if (tag.onClick && tag.href) {
                    commonProps.onClick = tag.onClick;
                  }
                  commonProps.text = tag.title;
                  commonProps.icon = tag.icon;
                  if (!tag.text) commonProps.overlay = tag.href;
                  if (tag.overlay) commonProps.overlay = tag.overlay;
                  return (
                    <Styles.LabelWrapper isFirst={index === 0} key={tag.id}>
                      <Label {...commonProps} />
                    </Styles.LabelWrapper>
                  );
                })}
            </Styles.TagsWrapper>
          </Styles.Info>
        </Styles.InfoWrapper>
      </Styles.Content>
    </Styles.ContentWrapper>
    {children && <Styles.ChildWrapper>{children}</Styles.ChildWrapper>}
    {actions && <Styles.Actions>{actions}</Styles.Actions>}
  </CardSimple>
);

InfluencerHeaderCardMobile.propTypes = {
  avatar: PropTypes.string,
  image: PropTypes.string,
  title: PropTypes.string,
  isVerified: PropTypes.bool,
  tags: PropTypes.arrayOf(Array),
  actions: PropTypes.oneOfType([PropTypes.node, PropTypes.any]),
  children: PropTypes.oneOfType([PropTypes.node, PropTypes.any]),
};

InfluencerHeaderCardMobile.defaultProps = {
  avatar: undefined,
  image: undefined,
  title: undefined,
  isVerified: false,
  tags: [],
  actions: undefined,
  children: undefined,
};

export default InfluencerHeaderCardMobile;
