import A from '@buzzguru/ui/A';
import AccountCircleIcon from '@buzzguru/ui/assets/icons/account-circle';
import EyeIcon from '@buzzguru/ui/assets/icons/eye';
import InvolvementIcon from '@buzzguru/ui/assets/icons/ic-involvement';
import PriceIcon from '@buzzguru/ui/assets/icons/ic-price';
import ThumbsUpIcon from '@buzzguru/ui/assets/icons/ic-thumbs-up';
import Typography from '@buzzguru/ui/Typography2';
import T from '@lskjs/t';
import { formatter } from '@lskjs/utils/formatter';
import get from 'lodash/get';
import { observer } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import beauty from '../../utils/beauty';
import InfluencerHeaderCardMobile from '../InfluencerHeaderCardMobile';
import { ColumnStatistic, StatsWrapperMobile } from './ChannelHeaderInfoMobile.styles';

const ChannelHeaderInfoDesktop = (props) => {
  const { entity, tags } = props;
  const { item } = entity;

  let erMedianValue = get(item, 'medians.er') ? get(item, 'medians.er').toFixed(2) : 0;
  if (erMedianValue > 100) erMedianValue = 100;
  const erMedian = `${erMedianValue}%`;

  const stats = [
    {
      id: 'subscribers',
      value: formatter(Math.ceil(get(item, 'channelInfo.subscribers', 0))),
      title: <T name="channelHeaderInfo.subscribersCount" />,
      icon: <AccountCircleIcon color="#7070ff" />,
    },
    {
      id: 'avgViews',
      value: formatter(Math.ceil(get(item, 'average.analytics.views', 0))),
      title: <T name="channelHeaderInfo.averageViewsCount" />,
      icon: <EyeIcon color="#7070ff" />,
    },
    {
      id: 'avgLikes',
      value: formatter(Math.ceil(get(item, 'average.analytics.likes', 0))),
      title: <T name="channelHeaderInfo.averageLikesCount" />,
      icon: <ThumbsUpIcon color="#7070ff" />,
    },
    {
      id: 'price',
      value: `$${beauty(get(item, 'average.avgPriceMin', 0) / 100)} - $${beauty(
        get(item, 'average.avgPriceMax', 0) / 100,
      )}`,
      title: <T name="channelHeaderInfo.intergrationCosts" />,
      icon: <PriceIcon color="#7070ff" />,
    },
    {
      id: 'er',
      value: erMedian,
      title: <T name="channelHeaderInfo.er" />,
      icon: <InvolvementIcon color="#7070ff" />,
    },
  ];

  return (
    <InfluencerHeaderCardMobile
      avatar={get(item, 'avatars.300', get(item, 'channelInfo.avatar'))}
      image={get(item, 'poster.704')}
      title={get(item, 'channelInfo.title')}
      isVerified={get(item, 'channelInfo.verified')}
      tags={tags}
    >
      <StatsWrapperMobile>
        {stats.slice(0, 3).map((el, idx) => {
          const value = el.value.replace(/\s/g, '');
          return (
            <ColumnStatistic key={`${el.value}-${idx}`}>
              {el.icon}
              <Typography view="huge-1" style={{ paddingTop: 5 }}>
                {beauty(Number(value))}
              </Typography>
            </ColumnStatistic>
          );
        })}
      </StatsWrapperMobile>
    </InfluencerHeaderCardMobile>
  );
};

ChannelHeaderInfoDesktop.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  prefix: PropTypes.string.isRequired,
  onListClick: PropTypes.func.isRequired,
  stats: PropTypes.arrayOf(Array),
  tags: PropTypes.arrayOf(Array),
  links: PropTypes.arrayOf(Array),
};

ChannelHeaderInfoDesktop.defaultProps = {
  stats: [],
  tags: [],
  links: [],
};

export default observer(ChannelHeaderInfoDesktop);
